<template>
  <div class="archive">
    <div v-if="folderName">
      <div class="header">
        <q-btn
          class="head-btn"
          :color="btnColorCheckFiles"
          @click="checkFiles"
          label="Проверить файлы"
          :icon-right="btnIconCheckFiles"
          :loading="isCheckFiles"
          :disable="isCheckFiles"
        >
          <template v-slot:loading>
            <q-spinner-tail size="sm" />
          </template>
        </q-btn>
        <q-btn
          class="head-btn"
          :color="btnColorCreateArchive"
          @click="createArchive"
          label="Создать архив"
          :icon-right="btnIconCreateArchive"
          :loading="isCreateArchive"
          :disable="isCreateArchive"
        >
          <template v-slot:loading>
            <q-spinner-tail size="sm" />
          </template>
        </q-btn>
      </div>
      <section class="wrapper">
        <div class="item item-check">
          <div class="false">
            <h6 class="title">Не прошли проверку</h6>
            <div class="check-block" v-for="(check, index) of arrCheckFalse" :key="index">
              <p>
                <q-icon name="close" color="negative" />
                {{ check.name }}
                <q-icon name="info" color="info" v-if="check.warning">
                  <q-tooltip>{{ check.warning }}</q-tooltip>
                </q-icon>
              </p>
            </div>
          </div>
          <div class="true">
            <h6 class="title">Прошли проверку</h6>
            <div class="check-block" v-for="(check, index) of arrCheckTrue" :key="index">
              <p>
                <q-icon name="check" color="positive" />
                {{ check }}
              </p>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="archive-block" v-for="(archive, index) of arrArchiveCreated" :key="index">
            <p>
              <b>Имя:</b>
              {{ archive.name }}
            </p>
            <p>
              <b>Размер:</b>
              {{ archive.size }} ({{ archive.size | convertBytes() }})
              <q-btn
                flat
                round
                color="primary"
                icon="content_copy"
                size="xs"
                @click="copySize(archive.size)"
              />
            </p>
          </div>
        </div>
      </section>
    </div>
    <div v-else>
      <h6 class="no-file">Файл не выбран</h6>
    </div>
  </div>
</template>

 <script>
import { mapState } from 'vuex';
import { saveBtn } from '@/mixins/saving';

function convertBytes(bytes, decimals = 2) {
  if (bytes === 0) return '0 Bytes';

  const k = 1024;
  const dm = decimals < 0 ? 0 : decimals;
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
}

export default {
  name: 'Archive',
  mixins: [saveBtn],
  data: function () {
    return {
      autoCheck: false,
      isCheckFiles: false,
      btnColorCheckFiles: 'primary',
      btnIconCheckFiles: 'playlist_add_check',
      isCreateArchive: false,
      btnColorCreateArchive: 'primary',
      btnIconCreateArchive: 'create',
      arrArchiveCreated: [],
      arrCheckTrue: [],
      arrCheckFalse: [],
    };
  },
  filters: {
    convertBytes(bytes, decimals) {
      return convertBytes(bytes, decimals);
    },
  },
  computed: {
    ...mapState({
      baseURL: (state) => state.baseURL,
      folderName: (state) => state.folderName,
      globalOptions: (state) => state.settings.options,
    }),
    redirectAfterFiles: {
      get() {
        return this.$store.state.redirectAfterFiles;
      },
      set(value) {
        this.$store.commit('setRedirectAfterFiles', value);
      },
    },
  },
  watch: {},
  methods: {
    copySize(size) {
      navigator.clipboard.writeText(convertBytes(size));
    },
    async checkFiles() {
      this.isCheckFiles = true;

      let response, result;
      try {
        response = await fetch(this.baseURL + 'check_files', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({ folderName: this.folderName }),
        });
        result = await response.json();

        if (result.error && result.error !== '') {
          this.saveAction('btnIconCheckFiles', 'btnColorCheckFiles', false); //mixin
          console.error(result.error);
        } else {
          this.saveAction('btnIconCheckFiles', 'btnColorCheckFiles'); //mixin
        }
      } catch (error) {
        this.saveAction('btnIconCheckFiles', 'btnColorCheckFiles', false); //mixin
        console.error(error);
      }

      this.arrCheckTrue = result.arrCheckTrue;
      this.arrCheckFalse = result.arrCheckFalse;

      this.isCheckFiles = false;
    },
    async createArchive() {
      this.isCreateArchive = true;
      this.arrArchiveCreated = [];

      if (this.autoCheck) {
        await this.checkFiles();
        if (this.arrCheckFalse.length !== 0) {
          this.saveAction(
            'btnIconCreateArchive',
            'btnColorCreateArchive',
            false
          ); //mixin
          this.isCreateArchive = false;
          return;
        }
      }

      let response, result;
      try {
        response = await fetch(this.baseURL + 'create_archive', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({ folderName: this.folderName }),
        });
        result = await response.json();

        if (result.error && result.error !== '') {
          this.saveAction(
            'btnIconCreateArchive',
            'btnColorCreateArchive',
            false
          ); //mixin
          console.error(result.error);
        } else {
          this.saveAction('btnIconCreateArchive', 'btnColorCreateArchive'); //mixin
        }
      } catch (error) {
        this.saveAction('btnIconCreateArchive', 'btnColorCreateArchive', false); //mixin
        console.error(error);
      }

      this.arrArchiveCreated = result.data;

      this.isCreateArchive = false;
    },
    getGlobalOptions() {
      this.autoCheck = this.globalOptions.checkArchive.autoCheck;
    },
  },
  mounted() {
    this.redirectAfterFiles = '/archive';
  },
  created() {
    this.getGlobalOptions();
  },
};
</script>

 <style  lang="scss" scoped>
.wrapper,
.header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}

.header {
  margin-bottom: 2rem;
}

.archive-block {
  margin-bottom: 1rem;
}

.archive-block p {
  margin-bottom: 0.3rem;
}

.item-check .title {
  margin: 0;
}

.item-check .check-block {
  margin-bottom: 0.2rem;
}

.item-check .check-block p {
  margin: 0;
  font-size: 110%;
}

.false {
  margin-bottom: 1rem;
}
</style>